import common from '@/common/common.js'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			userInfo:[],
			row:[],
			hotline:'15988355159',
		}
	},
	onLoad: function() {
		var that = this

		that.getJoinContent();			
		that.getHotline();
		
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		
		that.getJoinContent();
		that.getHotline();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		//拨打客服电话
		phoneContact(){
			var that = this
			uni.makePhoneCall({
				phoneNumber:that.hotline
			})
		},
		//获取客服电话
		getHotline(){
			var that = this
			uni.request({
				url: common.apiUrl + 'user/getHotline',
				method: 'GET',
				data: {
				},
				success: res => {
					that.hotline = res.data.hotline
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//提交
		formSubmit(e){
			var that = this
			var data = e.detail.value;
			var shop_name = data.shop_name
			var shop_addr = data.shop_addr
			var shop_contacter = data.shop_contacter
			var shop_mobile = data.shop_mobile
			var userInfo = uni.getStorageSync('userInfo');
			var userid = userInfo.users_id
			
			if(shop_name == '' ){
				uni.showToast({ title: '请输入店铺名称',duration: 1500,icon :'none' });
				return false
			}
			if(shop_addr == '' ){
				uni.showToast({ title: '请输入店铺地址',duration: 1500,icon :'none' });
				return false
			}
			
			if(shop_contacter == '' ){
				uni.showToast({ title: '请输入联系人',duration: 1500,icon :'none' });
				return false
			}				
			if(shop_mobile == '' || shop_mobile.length != 11){					
				uni.showToast({title: '请输入推荐人手机号',duration: 1500,icon :'none'});
				return false
			}
			
			uni.showLoading({
				title: '处理中'
			});
			
			uni.request({
				url: common.apiUrl + 'shop/shopJoin',
				method: 'GET',
				data: {
					shop_name:shop_name,
					shop_addr:shop_addr,
					shop_contacter:shop_contacter,
					shop_mobile:shop_mobile,
				},
				success: res => {
					uni.hideLoading();
					var data = res.data;
					
					if(data.code == 0){
						uni.showModal({
							title:'提示',
							content:'提交成功，等待客服处理',
							showCancel: false,
							confirmText: '确定',
							success: res => {
								if(res.confirm){
									uni.navigateBack();
								}
							},
						})
						
					}else{
						uni.showToast({
							title: data.msg,
							duration: 1500,
							icon:'none'
						});
					}
					
				},
				fail: () => {},
				complete: () => {}
			});
			
			
			
		},
		
		getJoinContent(){
			var that = this
			uni.request({
				url: common.apiUrl + 'ads/getJoinContent',
				method: 'GET',
				data: {},
				success: res => {
					that.row = res.data.row
				},
			});
		}
	}
}
